import React from 'react';
import numeral from 'numeral';
import {Circle, Popup} from 'react-leaflet';
const casesTypeColors = {
	cases: {
		hex: '#g2g2f2',
		rgb: 'rgb(70, 70, 70,0.5)',
		half_op: 'rgba(70, 70, 70, 0.5)',
		multiplier: 450,
	},
	recovered: {
		hex: '#7dd71d',
		rgb: 'rgb(125, 215, 29)',
		half_op: 'rgba(125, 215, 29, 0.5)',
		multiplier: 400,
	},
	deaths: {
		hex: '#fb4443',
		rgb: 'rgb(251, 68, 67)',
		half_op: 'rgba(251, 68, 67, 0.5)',
		multiplier: 1000,
	},
};

const ShowCountryOnMap = ({country, casesType}) => {
	return (
		<Circle center={[country.lat, country.long]} color={casesTypeColors[casesType].rgb} fillColor={casesTypeColors[casesType].rgb} fillOpacity={0.4} radius={Math.sqrt(country[casesType]) * casesTypeColors[casesType].multiplier}>
			<Popup>
				<div className="info-container">
					<div className="info-flag" style={{backgroundImage: `url(${country.flag})`}}></div>
					<div className="info-name">{country.country}</div>
					<div className="info-confirmed">Cases: {numeral(country.cases).format('0,0')}</div>
					<div className="info-recovered">Recovered: {numeral(country.recovered).format('0,0')}</div>
					<div className="info-deaths">Deaths: {numeral(country.deaths).format('0,0')}</div>
				</div>
			</Popup>
		</Circle>
	);
};

export default ShowCountryOnMap;
